.arsenal {
    width: 100%;
    height: 20%;
    background-color: var(--secondary-color);
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
      
    display: grid;
    grid-template-columns: repeat(40, 1fr);
    grid-template-rows: repeat(12, 1fr);
    grid-gap: 2px;
}


