.board {
    width: 100%;
    height: 60%;
    background-color: var(--primary-color);
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
  
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
    grid-gap: 2px;
}


